<template>
  <v-card raised elevation="12">
    <v-card-text>
      <h1 color="primary">Iniciar sesión</h1>
    </v-card-text>
    <v-card-subtitle>Ingresa tu contraseña</v-card-subtitle>
    <v-card-actions>
      <v-col>
        <v-form ref="form" v-model="valid">
            <v-text-field v-on:keydown.enter.prevent="loginWithPassword" type="password" label="Escribe tu contraseña" :rules="passwordRules" outlined required v-model="password"></v-text-field>
            <v-btn type="button" :disabled="!valid" raised color="primary" @click="loginWithPassword" large block>Iniciar Sesión</v-btn>
        </v-form>
      </v-col>
    </v-card-actions>
    <v-card-text>
      <v-btn text small color="primary" @click="goToResetPassword">Olvidaste tu contraseña</v-btn>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapActions } from "vuex"

export default {
    data: function() {
        return {
            valid: true,
            password: null,
            passwordRules: [
                v => !!v || 'Password is required'
            ]
        };
    },
    methods: {
        ...mapActions("login", [
            "loginAction"
        ]),
        loginWithPassword () {
            if (this.$refs.form.validate()) {
                this.loginAction(this.password)
            }            
        },
        goToResetPassword() {
          this.$router.push({ name: "resetPassword" });
        }
  },
}
</script>

<style lang="scss">
.img {
  width: 250px;
  margin: auto;
  background: var(--color-primary);
  border-radius: 25px
}

</style>